<template>
    <div class="piece-container" :class="[value, mouseover]" @mouseleave="setMouseOut" @mouseenter="setMouseover" @click="$emit('guess', {x, y})">
        <!-- {{value}} -->
    </div>
</template>

<script>
export default {
    props:{
        value: String,
        x: Number,
        y: Number
    },
    data(){
        return {
            mouseover: null
        };
    },
    methods: {
        setMouseover(){
            this.mouseover = 'mouse';
        },
        setMouseOut(){
            this.mouseover =null;
        }
    }
}
</script>

<style scoped>
    .piece-container{
        background-color: purple;
        border: 3px solid black;

        width: 40px;
        height: 40px;

        text-align: center;

        transition-property: all;
        transition-duration: 0.2s;
        
    }

    /* .piece-container:hover{
        border: 3px dashed black;
        border-radius: 10%;
        width: 50px;
        height: 50px;
    } */

    .mouse{
        background-color: cyan;
        border: 3px dashed black;
        border-radius: 10%;
        width: 50px;
        height: 50px;
    }

    .e{
        background-color: lightgrey;
    }
    .h{
        background-color: red;
    }
    .m{
        background-color: white;
    }
    .s{
        background-color: cyan;
    }
</style>