<template>
    <div class="game-container">
        <p>Game Id: {{$route.params.id}}</p>
        <Board :board="state.player1_board" :player="1" v-on:guess="makeGuess"/>
    </div>
</template>

<script>
import io from 'socket.io-client'
import Board from '../components/Board/Board.vue'

export default {    
    data(){
        return {
            socket: null,
            player2_joined: false,
            player_num: null,
            game_id: null,
            player_board: [],
            player_turn: 1,
            game_start: false,
            players_joined: 0,
            state: null
        };
    },
    created(){
        this.game_id = this.$route.params.id
        
        var connectionOptions =  {
            "force new connection" : true,
            "reconnectionAttempts": "Infinity", 
            "timeout" : 10000,                  
            "transports" : ["websocket"]
        };

        this.socket = io.connect('http://gh-api.com:3001',connectionOptions);
        this.socket.emit('new_connection', {
            players_joined: this.players_joined,
            game_id: this.game_id
        })
        
        this.socket.on('give_game_state', async (event) => {
            this.updateState(event);
        })
    },
    methods: {
        updateState(event){
            if(event.game_id === this.game_id){
                this.players_joined = event.players_joined;
                this.player_board = event.player1_board;
                this.state = event;
            }
        },

        makeGuess(guess){
            console.log(guess)
            this.socket.emit('guess', {game_id: this.game_id, player: 1, x: guess.x, y: guess.y})
        }
    },
    components: {
        Board
    }
}
</script>