<template>
    <div class="home-container">
        <Header title="Battleships"/>
        <Button text="Start New Game" :action="createNewGame"/>
    </div>
</template>

<script>
    import Header from '../components/Header.vue'
    import Button from '../components/Button.vue' 
    import { uuid } from 'vue-uuid'

    export default {
        name: "home",
        components: 
        {
            Header,
            Button
        },
        methods: {
            createNewGame()
            {
                this.$router.push(`list/${uuid.v4()}`);
            }
        },
        
    }
</script>